@import url(https://fonts.googleapis.com/css2?family=Lato:wght@700&family=Roboto+Condensed&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Encode+Sans+Condensed:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@700&family=Saira+Extra+Condensed:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;900&display=swap);
h1.nav__title {
  color: yellowgreen !important;
  font-family: 'Encode Sans Condensed', sans-serif;
}
.nav {
  position: fixed !important;
  color: yellowgreen !important;
  z-index: 60 !important;
  padding: 10px;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App,
.item {
  background-color: #111 !important;
  color: #fff !important;
}
.ui.secondary.pointing.menu .active.item {
  border-color: #fff !important;
}

body,
.ui.segment.active.tab {
  background-color: #111 !important;
}
.ui.segment.active.tab {
  width: 95%;
  margin: auto;
}
.slick-dots li.slick-active button:before {
  opacity: 0.75;
  color: #fff;
}
.slick-dots li button:before {
  opacity: 0.25;
  color: #fff;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.banner {
  height: 450px;
}
.banner__contents {
  color: #ffffff;
  object-fit: contain;
  text-align: left;
  margin-left: 30px;
  padding-top: 140px;
  height: 190px;
}

.banner__title {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
  letter-spacing: 0.5px;
}
.banner__description {
  width: 45rem;
  line-height: 1.3;
  padding-top: 1rem;
  font-size: 0.8rem;
  text-align: left;
  max-width: 360px;
  height: 80px;
}
.banner__button {
  cursor: pointer;
  color: #ffffff;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  margin-right: 1rem;
  padding: 0.5rem 2rem;
  background-color: rgba(51, 51, 51, 0.5);
}
.banner__button:hover {
  color: #111;
  background-color: #f1f1f1;
  transition: all 0.2s;
}
.banner__gradient {
  width: 100%;
  height: 40%;
  bottom: 0;
  position: absolute;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#000000+0,000000+100&0+0,0.65+100 */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0) 0%,
    rgba(17, 17, 17, 1) 100%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#a6000000',GradientType=0 ); /* IE6-9 */
}
header.banner {
  position: relative;
}
button.ui.grey.inverted.button {
  width: 150px;
}
.poster__buttons ul {
  padding: 0;
}
a,
a:hover {
  color: unset;
}
ul.banner__buttons {
  list-style-type: none;
  display: inline-flex;
  padding: 0;
}

img.row__poster {
  object-fit: contain;
  position: relative;
  width: 100%;
  max-width: 340px;
}

.poster__card .movie__text {
  width: 100%;
  top: 0;
  color: rgba(255, 255, 255, 0.7);
  height: 100%;
  transition: opacity 500ms ease;
}
.poster__card:hover .movie__text {
  opacity: 1;
}
.row__posters::-webkit-scrollbar {
  display: none;
}
.poster__buttons {
  display: inline;
}
poster__buttons ul li {
  list-style-type: none !important;
}
.movie__text h3.ui.header {
  color: #ffffff;
}
img.row__poster {
  margin: auto;
}
.poster__button {
  cursor: pointer;
  color: #f1f1f1;
  background-color: #111;
  width: 150px;
  margin: 5px;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  margin-right: 1rem;
  padding: 0.5rem 0;
}
.poster__button:hover {
  color: #ffffff;
  background-color: rgba(51, 51, 51, 0.5);
  transition: all 0.2s;
}
h3.ui.header {
  margin-top: 10px !important;
}

.poster__buttons ul {
  list-style-type: none;
}
.poster__buttons ul li {
  margin: 10px 0;
}
.poster__buttons button.ui.button {
  background-color: #f1f1f1;
}
a.poster__button {
  cursor: pointer;
  display: inline-block;
  min-height: 1em;
  outline: 0;
  border: none;
  vertical-align: baseline;
  background: #f1f1f1 none;
  color: #111;
  font-family: Lato, 'Helvetica Neue', Arial, Helvetica, sans-serif;
  margin: 0 0.25em 0 0;
  margin-top: 0px;
  margin-right: 0.25em;
  margin-bottom: 0px;
  margin-left: 0px;
  padding: 0.78571429em 1.5em 0.78571429em;
  text-transform: none;
  text-shadow: none;
  font-weight: 700;
  line-height: 1em;
  font-style: normal;
  text-align: center;
  text-decoration: none;
  border-radius: 0.28571429rem;
  box-shadow: 0 0 0 1px transparent inset, 0 0 0 0 rgba(34, 36, 38, 0.15) inset;
  -webkit-user-select: none;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
  transition: opacity 0.1s ease, background-color 0.1s ease, color 0.1s ease,
    box-shadow 0.1s ease, background 0.1s ease;
  will-change: '';
  -webkit-tap-highlight-color: transparent;
}

span.meta-data,
.movie__overview,
h2.ui.header {
  font-family: Lato, sans-serif;
}
.movie__button {
  cursor: pointer;
  background-color: #f1f1f1;
  border: 1px solid #f1f1f1;
  color: #111;
  width: 150px;
  margin: 5px;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  margin-right: 1rem;
  padding: 0.5rem 0;
}
.movie__button:hover {
  background-color: #111;
  color: #f1f1f1;
  border: 1px solid #f1f1f1;
}
h2.ui.header {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 2.2em;
}
.movie__overview {
  font-family: 16px;
  letter-spacing: 0.5px;
}
span.meta-data,
.tagline,
span.genre-list,
span.meta-data {
  color: greenyellow;
}
.tagline {
  display: block;
  padding: 0 0 40px 0;
  font-size: 25px;
}
.row.padding.release-details {
  display: grid;
  grid-template-columns: 1fr 1fr;
  font-size: 16px;
  padding: 20px 0;
}

.genre-list {
  padding: 20px 0;
}
span.meta-data {
  display: block;
  font-size: 23px;
  line-height: 1.1em;
}
.tagline,
.row.padding.release-details {
  font-family: 'Saira Extra Condensed', sans-serif;
}
#outer__container,
#card__container {
  height: 100vh;
}
@media (min-width: 768px) {
  .movie__detail {
    padding: 25px;
  }
  .movie__stats {
    font-size: 20px;
  }
  h2.ui.header {
    font-size: 30px;
    letter-spacing: 0.3px;
  }
}
.movie__detail {
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
}
h2.ui.header {
  color: #fff;
}

div#gradient__container {
  width: 100%;
  height: 100%;
  bottom: 0;
  position: absolute;
  background: linear-gradient(
    to bottom,
    rgba(17, 17, 17, 1) 0%,
    rgba(17, 17, 17, 0.7) 50%,
    rgba(17, 17, 17, 1) 100%
  );
}
.movie__card {
  display: grid;
  grid-template-areas: 'image details';
  grid-template-columns: 1fr 1fr;
  width: 70%;
  max-width: 790px;
  z-index: 30;
  margin: auto;
}
.movie__image {
  grid-area: image;
}
.movie__detail {
  grid-area: details;
}
div#card__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (max-width: 950px) {
  .movie__card {
    width: 90%;
  }
  .movie__card,
  .movie__detail {
    display: block;
    padding: 20px;
    margin: auto;
  }
  .movie__image {
    display: none;
  }
  .movie__detail button.movie__button {
    width: 100%;
    max-width: 292px;
    margin: 0 auto;
  }
  span.meta-data {
    font-size: 17px;
  }
  .movie__stats {
    font-size: 15px;
  }
}

footer {
  width: 100%;
  padding: 100px 0;
  text-align: center;
  color: #fff;
}
p.powered-by {
  display: inline;
  font-family: Lato, sans-serif;
  font-weight: 900;
  padding-top: 50px;
}
img.TMDB {
  width: 100px;
}
img.stripe {
  width: 60px;
}

.icon__container svg {
  font-size: 500%;
  color: greenyellow;
}
.icon__container {
  text-align: center;
  padding-top: 40px;
}
.success__text,
.another__text {
  color: #ffffff;
}
.success__image {
  width: 200px;
  height: auto;
  margin: auto;
}
.success__text,
.another__text {
  text-align: center;
  font-family: 'Lato', sans-serif;
}
p.success__text {
  font-size: 35px;
}

span.bold__text {
  font-weight: 900;
  display: block;
}

p.another__text {
  font-size: 16px;
  padding-top: 35px;
}

